var request = require('superagent');

const url = "http://localhost:3000";

const Usuario = {
	listAll: function (next) {
		request
		.get(url + "/api/v1/usuario")
		.set('Accept', 'application/json')
		.end(function(err, res){
			console.log(err);
			console.log(res);
			if (res) {
				if (res.text) {
					try {
						
						var obj = JSON.parse(res.text);
						console.log(obj);

						if (err || !res.ok) {
							next({success: false, msg: "Resposta invlida do servidor.", data: obj});
						} else {
							next({success: true, msg: "Cadastro realizado com sucesso.", data: obj});
						}							
					} catch (error) {
						next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
					}
				} else {
					next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
				}
			} else {
				next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
			}
			});			
	},
	searchByName: function (name, next) {
		request
		.get(url + "/api/v1/usuario/search/" + name)
		.set('Accept', 'application/json')
		.end(function(err, res){
			console.log(err);
			console.log(res);
			if (res) {
				if (res.text) {
					try {
						
						var obj = JSON.parse(res.text);
						console.log(obj);

						if (err || !res.ok) {
							next({success: false, msg: "Resposta invlida do servidor.", data: obj});
						} else {
							next({success: true, msg: "Cadastro realizado com sucesso.", data: obj});
						}							
					} catch (error) {
						next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
					}
				} else {
					next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
				}
			} else {
				next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
			}
			});			
	},
	create: function (usuario, next) {
		if (usuario) {
			if(usuario.nome && usuario.nome.length === 0) 
				next({success: false, msg: "O campo Nome  obrigatrio."});
			if(usuario.tipo && usuario.tipo.length === 0) 
				next({success: false, msg: "O campo Tipo  obrigatrio."});
			if(usuario.email && usuario.email.length === 0) 
				next({success: false, msg: "O campo Email  obrigatrio."});
			if(usuario.senha && usuario.senha.length === 0) 
				next({success: false, msg: "O campo Senha  obrigatrio."});
			if(usuario.telefone && usuario.telefone.length === 0) 
				next({success: false, msg: "O campo Telefone  obrigatrio."});
			if(usuario.matricula && usuario.matricula.length === 0) 
				next({success: false, msg: "O campo Matricula  obrigatrio."});
			if(usuario.logradouro && usuario.logradouro.length === 0) 
				next({success: false, msg: "O campo Logradouro  obrigatrio."});
			if(usuario.numero && usuario.numero.length === 0) 
				next({success: false, msg: "O campo Numero  obrigatrio."});
			if(usuario.bairro && usuario.bairro.length === 0) 
				next({success: false, msg: "O campo Bairro  obrigatrio."});
			if(usuario.complemento && usuario.complemento.length === 0) 
				next({success: false, msg: "O campo Complemento  obrigatrio."});

			// Salvando
			request
			.post(url + "/api/v1/usuario")
			.send(usuario)
			.set('Accept', 'application/json')
			.end(function(err, res){
				console.log(err);
				console.log(res);
				if (res) {
					if (res.text) {
						try {
							
							var obj = JSON.parse(res.text);
							console.log(obj);

							if (err || !res.ok) {
								next({success: false, msg: "Resposta invlida do servidor.", data: obj});
							} else {
								next({success: true, msg: "Cadastro realizado com sucesso.", data: obj});
							}							
						} catch (error) {
							next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
						}
					} else {
						next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
					}
				} else {
					next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
				}
				});		
		} else {
			return {success: false, msg: "Erro na aplicao."};
		}
	},
	login: function(vLogin, vSenha, next){

		next({success: true, msg: "Login Realizado com sucesso.", data: null});	

		console.log("aqui");

		if (vLogin.length === 0) { // Aqui eu verifico se digitou algo no login
			next({success: false, msg: "O campo Email  Obrigatrio."});
			return;
		} else if (vSenha.length === 0) { // Aqui eu verifico se digitou algo no login
			next({success: false, msg: "O campo Senha  Obrigatrio."});
			return;
		}

		request
		.post(url + "/api/v1/usuario/login")
		.send({
					email: vLogin, 
					senha: vSenha
				})
		.set('Accept', 'application/json')
		.end(function(err, res){

			if (res) {
				if (res.text) {
					try {
						var obj = JSON.parse(res.text);
						console.log(obj);

						if (err || !res.ok) {
							next({success: false, msg: "Resposta invlida do servidor.", data: obj});
						} else {
							next({success: true, msg: "Login Realizado com sucesso.", data: obj});
						}							
					} catch (error) {
						next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
					}
				} else {
					next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
				}
			} else {
				next({success: false, msg: "Falha de comunicao com o servidor. Verifique sua conexo.", data: obj});
			}
		});
	}  
}

module.exports = Usuario;



